import { createMemoryRouter, MemoryRouterOpts } from 'react-router'

import Index from './pages/Index'
import NewQldQuotePage from './pages/quote/qld/sale/new'
import ViewQldQuotePage from './pages/quote/qld/sale/view'
import Notfound from './pages/Notfound'

export const createRouter = (options?: MemoryRouterOpts) =>
  createMemoryRouter(
    [
      {
        path: '/',
        element: <Index />,
      },
      {
        path: '/quote/qld/sale/new',
        element: <NewQldQuotePage />,
      },
      {
        path: '/quote/qld/sale/:quoteId',
        element: <ViewQldQuotePage />,
      },
      {
        path: '*',
        element: <Notfound />,
      },
    ],
    options,
  )
