import ReactDOM from 'react-dom/client'
import './global.css'

declare global {
  interface Window {
    loadApp: any
  }
}

import App, { Props } from './App'

// Setup MSW mock server in both development and production
// Certify MSW's Service Worker is available before starting React app
import('../mocks/browser')
  .then(async ({ worker }) => {
    return worker.start()
  }) // Run <App /> when Service Worker is ready to intercept requests
  .then(() => {
    window.loadApp = (rootElem: HTMLElement, props?: Props) => {
      const root = ReactDOM.createRoot(rootElem!)
      root.render(<App {...props} />)
    }

    const rootElem = document.getElementById('app-root')

    // initial path
    const path = rootElem?.dataset.path

    // viewing a quote
    const quoteId = rootElem?.dataset.quoteId

    window.loadApp(rootElem, { path, quoteId })
  })
