import axios, { AxiosError, AxiosRequestConfig } from 'axios'

export const api = {
  get: async (url: string, config?: AxiosRequestConfig) => {
    try {
      const response = await axios.get(url, config)
      return response.data
    } catch (error) {
      handleAxiosError(error, config)
    }
  },
  post: async (url: string, data: unknown, config?: AxiosRequestConfig) => {
    try {
      const response = await axios.post(url, data, config)
      return response.data
    } catch (error) {
      handleAxiosError(error, config)
    }
  },
}

function handleAxiosError(error: unknown, config?: AxiosRequestConfig) {
  const { response } = error as AxiosError<any>
  if (!response) throw error

  const { data: resBody } = response
  const { method, url, headers } = config ?? {}
  throw {
    type: response.statusText,
    code: response.status,
    errorCode: resBody.errorCode,
    message: resBody.message,
    body: resBody,
    request: { method, url, headers },
  }
}
