import { HTMLInputTypeAttribute } from 'react'
import { useFormContext } from 'react-hook-form'

type InputProps = {
  id?: string
  label?: string
  name: string
  defaultValue?: string
  required?: boolean
  type?: HTMLInputTypeAttribute
}

export default function Input(props: InputProps) {
  const {
    name,
    id = name,
    label,
    required,
    defaultValue,
    type = 'text',
  } = props
  const { register } = useFormContext()

  return (
    <div className="grid gap-0.5">
      {label && <label htmlFor={id}>{label}</label>}
      <input
        {...register(name)}
        id={id}
        type={type}
        defaultValue={defaultValue}
        required={required}
        className="block w-full bg-gray-50 border-gray-400 px-3 py-1.5 text-sm text-gray-900"
      />
    </div>
  )
}
