import { useForm } from 'react-hook-form'
import { zodResolver } from '@hookform/resolvers/zod'
import z from 'zod'
import Form from '../Form/Form'
import Input from '../Form/TextInput'
import { useMutation } from '@tanstack/react-query'
import { createQuote } from '@/api/quote.api'
import { useNavigate } from 'react-router'

const formSchema = z.object({
  code: z.string(),
  user: z.object({
    firstName: z.string(),
    lastName: z.string(),
    email: z.email(),
    phone: z.string(),
  }),
})

type FormValues = z.infer<typeof formSchema>

export default function QldSaleQuoteForm() {
  const navigate = useNavigate()
  const methods = useForm({
    resolver: zodResolver(formSchema),
    defaultValues: {
      user: {
        firstName: 'Jonathan',
        lastName: 'Redford',
        email: 'jonathan@fletcherdigital.co',
      },
    },
  })

  const quoteMutation = useMutation({
    mutationFn: createQuote,
    onSuccess: (quote) => {
      console.log(`✅✅✅✅✅✅ Quote successfully created: ${quote?._id}`)
      console.log({ quote })
      navigate(`/quote/qld/sale/${quote._id}`)
    },
    onError: (error) => {
      console.error('❌❌❌❌❌❌ Error creating quote', error)
    },
  })

  const onSubmit = (data: FormValues) => {
    console.log({ data })
    quoteMutation.mutate({
      ...data,
      transactionType: 'sale',
      state: 'QLD',
    })
  }

  return (
    <div className="text-left text-sm bg-white text-black p-8">
      <h2 className="text-lg text-monkey font-extrabold mb-4">
        Monkey Conveyancing Fees for a Seller
      </h2>
      <Form onSubmit={onSubmit} methods={methods}>
        <p className="font-bold">Select from one of the options below:</p>

        <div className="mt-4 grid gap-4">
          <div className="grid gap-1">
            {codes.map(({ value, label }) => (
              <div className="flex flex-row gap-2">
                <input
                  {...methods.register('code')}
                  type="radio"
                  id={value}
                  value={value}
                  required
                  className="text-monkey ring-monkey"
                />
                <label htmlFor={value}>{label}</label>
              </div>
            ))}
          </div>

          <Input name="user.firstName" label="First Name" required />
          <Input name="user.lastName" label="Last Name" required />
          <Input name="user.email" label="Email" type="email" required />
          <Input name="user.phone" label="Phone" />

          <div className="grid gap-0.5">
            <input
              type="submit"
              value="Show Me My Quote!"
              className="p-3 bg-monkey cursor-pointer text-white rounded-md transition-colors shadow-lg"
            />
          </div>
        </div>
      </Form>
    </div>
  )
}

const codes = [
  {
    value: 'SPSCDF2',
    label: 'Sale Professional Fee (on a fully signed contract)',
  },
  { value: 'SCR', label: 'Pre-signing contract review' },
  { value: 'SPCR', label: 'Contract review + conveyancing' },
  { value: 'SDF2', label: 'Draft Form 2*' },
  { value: 'SPDF2', label: 'Draft Form 2* + conveyancing' },
  { value: 'SPDCDF2', label: 'Draft contract + Form 2* + conveyancing' },
]
