import React, { memo } from 'react'
import Box from '../../components/Box'
import { Link, useNavigate } from 'react-router'

interface Props {
  path?: string
}

const Index: React.FC<Props> = memo((props) => {
  const navigate = useNavigate()
  const { path } = props

  if (path) {
    navigate(path)
    return
  }

  return (
    <>
      <Box>
        <h1 className="text-2xl">Monkey Conveyancing</h1>
      </Box>
      <Box>
        <Link to={{ pathname: '/quote/qld/sale/new' }}>Get a new quote</Link>
      </Box>
      <Box>
        <Link to={{ pathname: '/quote/qld/sale/68be3a3cbb8a08b2e98e022b' }}>
          View quote
        </Link>
      </Box>
    </>
  )
})
Index.displayName = 'Index'

export default Index
