import { getQuote } from '@/api/quote.api'
import QldSaleQuote from '@/components/quotes/qld.sale.quote'
import { useQuery } from '@tanstack/react-query'
import React, { memo } from 'react'
import { useParams } from 'react-router'

interface Props {}

const Index: React.FC<Props> = memo(() => {
  const { quoteId } = useParams()

  const quoteQuery = useQuery({
    queryKey: ['quote', quoteId],
    queryFn: () => getQuote(quoteId as string),
  })

  return (
    <>
      <QldSaleQuote quote={quoteQuery.data} />
    </>
  )
})
Index.displayName = 'Form'

export default Index
